import { Component, OnInit } from '@angular/core';
import {  UsersService} from '../users.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  students:any;
  StudentName:string;
  StudentSurname:string;
  StudentCourse:string;
  StudentNumber:number;
 
  
  constructor(private studentservice: UsersService){
   
  }

  //add new Student
  CreateStudent(){
    let student={};
    student['Name']=this.StudentName;
    student['Surname']=this.StudentSurname;
    student['Course']=this.StudentCourse;
    student["Numbers"]=this.StudentNumber;
    this.studentservice.CreateStudent(student).then(resp=>{
      this.StudentName="";
      this.StudentSurname="";
      this.StudentCourse="";
      this.StudentNumber=undefined;
      console.log(resp);
    })
    .catch(error=>{
      console.log(error);
  });
}
ngOnInit():void{
}
}

